import React, { Component } from 'react';

class DashCard extends Component {
  render() {
    const { course, enrolled, onBtnClick } = this.props;
    const enrollBtn = (
      <button value={course._id} onClick={onBtnClick} className= "btn font1 mybtn1"> </button>
    )
    const delteBtn = (
      <button value={course._id} onClick={onBtnClick} className="btn font1 mybtn1">      </button>
    )
    const onceCourse = (
      <div style={{margin:'5px'}}>
        <p className="card-text">     {course.courseYear}/{course.courseMonth}/{course.courseDay}  </p>
      </div>
    )
    const periodicCourse = (
      <div style={{margin:'5px'}}>
        <p className="card-text">.     {course.courseDayInWeek}    {course.NumberOfSessions}   </p>
      </div>
    )

    return (
      <div className="card text-center admin-card margin-top-admin">
        <div className="card-header">
          {course.courseName}
        </div>
        <div className="card-body">
          <h5 className="card-title">      {course.courseTeacher}   </h5>
          {course.periodic? periodicCourse: onceCourse}
          <p>{course.courseDetails}</p>
          {enrolled? delteBtn: enrollBtn }
        </div>
        <div className="card-footer">
          .   {course.students.length+10}          
        </div>
      </div>
    )
  }
}

export default DashCard